import React, { useState } from 'react'
import { SavingItem } from '../PivotMenu';
import { DefaultButton, FontIcon, IIconProps, SwatchColorPicker, TextField, Toggle, mergeStyles, Text } from '@fluentui/react';
import { useId } from '@fluentui/react-hooks';

export const colorCells = [
    { id: 'orange', label: 'orange', color: '#ca5010' },
    { id: 'cyan', label: 'cyan', color: '#038387' },
    { id: 'blueMagenta', label: 'blueMagenta', color: '#8764b8' },
    { id: 'greenYellow', label: 'greenYellow', color: '#986f0b' },
    { id: 'cyanBlue', label: 'cyanBlue', color: '#3473a5' },
];

const iconCells = [
    { id: 'a', label: 'AllCurrency' },
    { id: 'b', label: 'Money' },
    { id: 'c', label: 'Chart' },
    { id: 'd', label: 'PieDouble' },
];

export interface IEditorProps {
    data: SavingItem[],
    onChangeAttr: Function,
    onAddRow: Function,
    onDelete: Function,
}

function Editor(props: IEditorProps) {

    const { data, onChangeAttr, onAddRow, onDelete } = props;
    const baseId = useId('colorpicker');
    const editIcon: IIconProps = { iconName: 'Edit' };
    const doneIcon: IIconProps = { iconName: 'Completed' };
    const deleteIcon: IIconProps = { iconName: 'Delete' };
    const addIcon: IIconProps = { iconName: 'Add' };
    const iconClass = mergeStyles({
        fontSize: 22,
        height: 22,
        width: 22,
        margin: '2px',
    });

    const [editRow, setEditRow] = useState(-1);
    const formatMoney = (text: number) => {
        return ((`${text}`).replace(/\B(?=(?:(\d\d)+(\d)(?!\d))+(?!\d))/g, ','));
    }

    return (
        <div
            style={{
                marginTop: '5%',
                display: 'flex',
                flexWrap: 'wrap',
            }}>
            <div style={{
                height: '85vh',
                width: '100%',
                overflow: 'scroll',
            }}>
                {data.map((item, index) => {
                    return (editRow < 0 || editRow === index) ? (
                        <div key={index} style={{
                            width: '92%',
                            backgroundColor: item.colour,
                            borderRadius: '4px',
                            margin: '2% 2%',
                            textAlign: 'left',
                            alignItems: 'center',
                            padding: '2%',
                            
                        }}>
                            {editRow < 0 ? <div style={{
                                margin: '2% 2%',
                                display: 'flex',
                                justifyContent: 'space-between'
                            }}>
                                <div>
                                    <Text style={{color: 'white',}} key={item.header} variant='mediumPlus' nowrap block>
                                        {item.header || '-no-header-'}
                                    </Text>
                                    <Text style={{color: 'white',}} key={item.amount} variant='xxLarge' nowrap block>
                                        {formatMoney(item.amount)}
                                    </Text>
                                </div>
                                <div style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems:'center'
                                }}>
                                    <Toggle
                                        label="Retirement Fund"
                                        onText="Yes"
                                        offText="No"
                                        styles={{
                                            label:{
                                                color: 'white',
                                            },
                                            text:{
                                                color: 'white',
                                                fontWeight: 'bold',
                                            }
                                        }}
                                        checked={item.isRetirement}
                                        onChange={(e, checked) => {
                                            // console.log(checked);
                                            onChangeAttr(index, 'isRetirement', checked);
                                        }} />
                                    <div style={{
                                        margin: '2% 5%',
                                        display: 'flex',
                                        justifyContent: 'space-between',
                                        marginTop: '10px',
                                    }}>
                                        <DefaultButton
                                            text={'Edit'}
                                            iconProps={editIcon}
                                            onClick={() => setEditRow(index)}
                                            style={{
                                                padding: '2%',
                                                marginRight: '10px',
                                                borderRadius: '8px',
                                            }}
                                        />
                                        <DefaultButton
                                            text='Delete'
                                            iconProps={deleteIcon}
                                            onClick={() => onDelete(index)}
                                            style={{
                                                padding: '2%',
                                                borderRadius: '8px',
                                            }}
                                        />
                                    </div>

                                </div>


                            </div>
                                : <div style={{
                                    display: 'flex',
                                    justifyContent: 'space-between'
                                }}>
                                    <TextField
                                        label="Amount"
                                        readOnly={editRow < 0}
                                        defaultValue={`${item.amount}`}
                                        onBlur={(e) => {
                                            onChangeAttr(index, 'amount', +e.target.value);
                                        }} />
                                    <TextField
                                        label="Source"
                                        readOnly={editRow < 0}
                                        defaultValue={`${item.header}`}
                                        onBlur={(e) => {
                                            onChangeAttr(index, 'header', e.target.value);
                                        }} />

                                </div>}
                            {editRow >= 0 ? <SwatchColorPicker
                                columnCount={5}
                                cellHeight={30}
                                cellWidth={30}
                                cellShape={'circle'}
                                colorCells={colorCells}
                                selectedId={item.colour}
                                aria-labelledby={`${baseId}-circle`}
                                onChange={(event, id, color) => {
                                    onChangeAttr(index, 'colour', color)
                                }}
                            /> : undefined}

                            <div style={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                marginTop: editRow > 0 ? '10px' : '10px',
                            }}>
                                {editRow >= 0 ? <div style={{
                                    display: 'flex',
                                    justifyContent: 'space-between'
                                }}>
                                    {iconCells.map(icon => {
                                        return (
                                            <FontIcon
                                                key={icon.id}
                                                iconName={icon.label}
                                                className={iconClass}
                                                onClick={() => {
                                                    onChangeAttr(index, 'icon', icon.label);
                                                }}
                                                style={{
                                                    border: icon.label === item.icon ? '2px solid white' : '2px solid transparent',
                                                    borderRadius: '5px',
                                                    margin: '5px 2px',
                                                    padding: '3px',
                                                }} />
                                        )
                                    })}

                                </div> :
                                    undefined
                                }
                                {editRow >= 0 ? <DefaultButton
                                    text={'Done'}
                                    iconProps={doneIcon}
                                    onClick={() => setEditRow(-1)}
                                    style={{
                                        padding: '2%',
                                        // marginRight: '10px',
                                        borderRadius: '8px',
                                    }}
                                /> : undefined}

                            </div>
                        </div>

                    ) : undefined;
                })}
            </div>
            <DefaultButton
                text='Add New'
                iconProps={addIcon}
                onClick={() => onAddRow()}
                style={{
                    margin: '2% 2%',
                    borderRadius: '8px',
                }}
            />
        </div>
    )
}

export default Editor;